



























































.service-volume {
  width: 100%;
  height: 100%;
  .dataClass {
    width: 33.33%;
    height: 100%;
    float: left;
    position: relative;
    .content {
      height: 50px;
      width: 100%;
      position: absolute;
      bottom: 60%;
      .num {
        width: 100%;
        text-align: center;
        font-size: 22px;
      }
      .text {
        width: 100%;
        text-align: center;
        color: #00e5ff;
        font-size: 12px;
      }
    }
    &:nth-of-type(1) .num {
      color: #17f817;
    }
    &:nth-of-type(2) .num {
      color: #f8e917;
    }
    &:nth-of-type(3) .num {
      color: #e96c68;
    }
  }
  .imageDiv1 {
    background-image: url(../../../static/images/yueyang/ResourceCategory3.png);
    background-repeat: no-repeat;
    background-position: 60% 80%;
    background-size: 98%;
  }
  .imageDiv2 {
    background-image: url(../../../static/images/yueyang/ResourceCategory2.png);
    background-repeat: no-repeat;
    background-position: 50% 58%;
    background-size: 98%;
  }
  .imageDiv3 {
    background-image: url(../../../static/images/yueyang/ResourceCategory1.png);
    background-repeat: no-repeat;
    background-position: 40% 80%;
    background-size: 98%;
  }
}
