






















































































































































































































































































































































































































































.view-map {
  @font-face {
    font-family: 'digifacewide';
    src: url('../../../static/fonts/digifacewide.ttf');
  }
  position: relative;
  width: 100%;
  height: 100%;
  // 下拉框
  .select {
    z-index: 100000;
    position: absolute;
    top: 20px;
    right: 50px;
    .select-cont {
      width: 120px;
      height: 26px;
      color: #ffffffcc;
      outline: none;
      background-color: #035087;
      border: 1px solid #1c5f92;
      &-op {
      }
    }
  }
  // 总数据量
  .total {
    z-index: 100000;
    position: absolute;
    top: 70px;
    left: 50%;
    width: 280px;
    height: 120px;
    color: #ffffff;
    font-size: 18px;
    transform: translateX(-50%);
    .factor {
      position: relative;
      margin-bottom: 20px;
      display: flex;
      align-items: center;
      .title {
        font-size: 14px;
        margin-right: 10px;
      }
      .unit {
        margin-left: 10px;
        font-size: 12px;
        color: #fff243;
      }
      .numbg {
        display: block;
        margin-left: 4px;
        width: 26px;
        height: 30px;
        font-size: 24px;
        font-family: 'digifacewide', serif;
        line-height: 30px;
        text-align: center;
        background: url('../../../static/images/yueyang/dataTotal.png')
          no-repeat 0 0 / 100% 100%;
      }
      .number {
        position: absolute;
        top: -3px;
        left: 75px;
        font-size: 24px;
        letter-spacing: 12px;
        font-family: 'digifacewide', serif;
      }
    }
  }
  // 刻度标尺
  .scale {
    z-index: 100000;
    position: absolute;
    top: 80%;
    left: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #ffffff;
    font-size: 12px;
    .color {
      width: 15px;
      height: 60px;
      background: linear-gradient(#0672e6, #05c8e6);
    }
    .low {
      position: absolute;
      display: flex;
      align-items: center;
      top: 73px;
      left: 20px;
      .icon {
        margin-right: 8px;
        width: 0;
        height: 0;
        border-top: 5px solid #05c8e6;
        border-right: 5px solid #05c8e6;
        border-left: 5px solid transparent;
        border-bottom: 5px solid transparent;
      }
    }
    .high {
      position: absolute;
      display: flex;
      align-items: center;
      top: 5px;
      left: 20px;
      .icon {
        margin-right: 8px;
        width: 0;
        height: 0;
        border-right: 5px solid #0672e6;
        border-bottom: 5px solid #0672e6;
        border-top: 5px solid transparent;
        border-left: 5px solid transparent;
      }
    }
  }
  // 图例
  .legend {
    z-index: 100000;
    position: absolute;
    top: 80%;
    right: 30px;
    color: #fff;
    font-size: 12px;
    div {
      display: flex;
      margin-bottom: 5px;
      align-items: center;
      .legend-item {
        margin-right: 5px;
        width: 16px;
        height: 4px;
        background-color: rgb(59, 237, 154);
      }
      &:nth-of-type(2) {
        .legend-item {
          background-color: rgb(242, 191, 53);
        }
      }
      &:nth-of-type(3) {
        .legend-item {
          background-color: rgb(130, 95, 247);
        }
      }
    }
  }
  // 图表
  /deep/ .ol-overlay-container:hover {
    z-index: 999;
  }
}
