

































.DataSharingSituation {
  padding: 10px 20px;
  width: 100%;
  height: 100%;
  .text {
    color: #ffffffcc;
    text-indent: 2em;
    &-color {
      &:nth-of-type(1) {
        color: #0cb9d8;
        font-size: 14px;
      }
      &:nth-of-type(2) {
        color: #f0ec17;
        font-size: 14px;
      }
      &:nth-of-type(3) {
        color: #2dfd95;
        font-size: 14px;
      }
      &:nth-of-type(4) {
        color: #ff3b3b;
        font-size: 14px;
      }
    }
  }
  .charts {
    width: 100%;
    height: calc(~'100% - 63px');
  }
}
