





















































































.core-data-situation {
  padding: 10px 20px;
  width: 100%;
  height: 100%;
  .header {
    display: flex;
    margin-bottom: 5px;
    align-items: center;
    width: 100%;
    height: 30px;
    color: #0cb9d8;
    background-color: rgb(2, 63, 112);
    .text {
      width: 25%;
      text-align: center;
      font-weight: 700;
    }
  }
  .tbody {
    width: 100%;
    height: calc(~'100% - 35px');
    overflow: hidden;
    .trow {
      display: flex;
      margin-bottom: 5px;
      align-items: center;
      width: 100%;
      height: 30px;
      background-color: #023f70;
      color: #ffffffcc;
      .text {
        width: 25%;
        text-align: center;
      }
      &:hover {
        background-color: #075ba5;
        cursor: pointer;
      }
    }
  }
}
