
















































































































.home-page-content {
  width: 100%;
  height: 100%;
  background-color: #11294b;
  position: absolute;
  .home-title {
    position: relative;
    width: 100%;
    height: 64px;
    color: white;
    text-align: center;
    padding-bottom: 10px;
    background-position: center center;
    background-image: url('../../../static/images/yueyang/top.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    font-size: 24px;
    line-height: 52px;
    font-weight: bold;
    font-family: 'Microsoft YaHei';
    .title-name {
      text-shadow: 0 0 2px #fff;
      letter-spacing: 5px;
    }
    .department {
      position: absolute;
      top: 15%;
      left: 11%;
      font-size: 14px;
      color: #0c99c3;
      font-weight: 400;
    }
    .time {
      position: absolute;
      top: 15%;
      right: 20px;
      font-weight: 400;
      .nowTime {
        font-size: 16px;
        letter-spacing: 2px;
        color: #0cb9d8;
      }
      .nowDate {
        margin-left: 10px;
        font-size: 14px;
        color: #0c99c3;
        .week {
          margin-left: 10px;
        }
      }
    }
  }
  .home-content {
    height: calc(~'100% - 52px');
    width: 100%;
    &-row {
      padding: 10px;
      height: 100%;
      width: 100%;
      background: url('../../assets/spatialdata/bj.png') no-repeat;
      background-size: cover;
      .col-left {
        padding-right: 5px;
        height: 100%;
      }
      .col-center {
        height: 100%;
      }
      .col-right {
        padding-left: 5px;
        height: 100%;
      }
      .map-content {
        width: 100%;
        height: calc(~'96% + 20px');
        background: url('../../assets/spatialdata/centerbg.png') no-repeat;
        background-size: 100% 100%;
      }
    }
  }
}
